import React from 'react'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import { Link } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles((theme) => ({
  Navbar: {
    fontSize: '2em',
    backgroundColor: 'transparent',
    color:"white",
  },
  NavbarBrand: {
    fontSize: '1.2em',
  },
}))

export default function DegenNavbar(props) {
  const classes = useStyles()
  return (
    <Navbar className={classes.Navbar} expand="lg" fixed="top" variant="dark">
      <Link to="/" style={{ marginRight: 30, color: '#FFF' }}>
        <Navbar.Brand
            className={classes.NavbarBrand}
            style={{ color: '#FFF' }}
        >
          LAMBO DEGENERATES
        </Navbar.Brand>
      </Link>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="ml-auto">
          <Link to="/nitro" style={{ marginRight: 30, color: '#FFF' }}>
            NITRO
          </Link>
          <Link to="/dealership" style={{ marginRight: 30, color: '#FFF' }}>
            DEALERSHIP
          </Link>
          <Link to="#" disabled={true} style={{ marginRight: 30, color: 'gray' }}>
            SWAP
          </Link>
          <Link to="/about" style={{ marginRight: 30, color: '#FFF' }}>
            ABOUT
          </Link>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  )
}
