import React from 'react';

import { makeStyles } from '@material-ui/core/styles';
import { Box } from '@material-ui/core';
import LamboButton from "./LamboButton";
import {ReactComponent as TwitterIcon} from "../images/twitter.svg";
import {ReactComponent as DiscordIcon} from "../images/discord.svg";
import {ReactComponent as MediumIcon} from "../images/medium.svg";
import {ReactComponent as TelegramIcon} from "../images/telegram.svg";

const useStyles = makeStyles((theme) => ({
    bottomNav: {
        width: "100%",
        display: "flex",
        justifyContent: "space-around",
        flexWrap: "no-wrap",
        padding: `${theme.spacing(2)}px ${theme.spacing(5)}px`
    },
    logo: {
        fill: "white",
        width: "32px",
        [theme.breakpoints.up('lg')]:{
            width: "40px",
        }
    }
}))

export default function DegenFooter(props) {
  const classes = useStyles()
  return (
    <Box className={classes.bottomNav}>
        <LamboButton openUrl="https://twitter.com/LamboDegens">
        <TwitterIcon className={classes.logo}/>
        </LamboButton>
        <LamboButton openUrl="https://discord.com/invite/G7j3QJh">
          <DiscordIcon className={classes.logo}/>
        </LamboButton>
        <LamboButton openUrl="https://medium.com/@lambodegens/lambo-boosting-the-trust-and-value-in-the-defi-world-6fc8c369f7bc">
            <MediumIcon className={classes.logo}/>
        </LamboButton>
        <LamboButton openUrl="https://t.me/joinchat/SVEU7knIZrImEFy1dFd05Q">
            <TelegramIcon className={classes.logo}/>
        </LamboButton>
    </Box>
  )
}
