import React from 'react';
import {makeStyles} from "@material-ui/core/styles";
import {Box} from "@material-ui/core";
import lambo from "../images/lambo-icon.svg";

const useStyles = makeStyles((theme) => ({
    wrapper: {
        display: 'grid',
        gridTemplateColumns: "repeat(12,1fr)",
        position: "relative",
        cursor: "pointer"
    },
    contentWrapper: {
        zIndex: "1",
        gridColumn: "5 / 10",
        gridRow: "1",
        padding: "10px",
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
    },
    lamboWrapper: {
        gridColumn: "1 / -1",
        gridRow: "1",
        [theme.breakpoints.down('sm')]:{
            display: "none",
        }
    },
    lambo: {
        width: "100%",
        fill: "white"
    }
}));

export default function LamboButton(props){
    const classes = useStyles();
    const openUrl = () => {
        window.open(props.openUrl)
    }
    return (
        <Box className={classes.wrapper+" "+props.className} onClick={openUrl}>
            <div className={classes.contentWrapper}>
                {props.children}
            </div>
            <div className={classes.lamboWrapper}>
                <img className={classes.lambo}  src={lambo} alt={"Lambo side"}/>
            </div>
        </Box>
    );
}
