/* React Imports */
import React from 'react'
import Web3 from 'web3'

/* Material Imports */
import Container from '@material-ui/core/Container'
import CssBaseline from '@material-ui/core/CssBaseline'
import { withStyles } from '@material-ui/core/styles';

/* React-Router */
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'

/* React Components */
import Homepage from './components/Homepage'
import NitroPage from './components/Nitro'
import DealershipPage from './components/Dealership'
import DegenSwapPage from './DegenSwap'
import DegenNavbar from './components/DegenNavbar'
import About from './components/About'

/* CSS */
import './App.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import DegenFooter from './components/DegenFooter'
import Box from '@material-ui/core/Box'
import moon from './images/moon-footer.png'
import stars from './images/stars.png'


/* CSS Styles */
const useStyles = {
  root: {
    backgroundColor: 'black',
    margin: '0',
    height: '100%',
    width: '100%',
    backgroundRepeat: "no-repeat",
    backgroundPosition: "center top",
    backgroundSize: "100%",
  },
  main: {
    height: '100%',
    minHeight: "100vh",
    margin: '0',
    display: 'flex',
    flexDirection: 'column',
    backgroundSize: "100%",
    backgroundPosition: "bottom",
    backgroundRepeat: "no-repeat",
  },
  navContainer: {
    flex: '0 0 auto',
  },
  contentContainer: {
    marginTop: "50px",
    flex: '1 1 auto',
    display: "flex",
    flexDirection: "column",
  },
  footerContainer: {
    flex: '0 0 auto',
  },
  background: {
    position: 'relative',
    height: '100%',
    width: '100%',
    '& img': {
      position: 'absolute',
      width: "100%",
      left: "0",
    },
  },
  moon: {
    position: 'absolute',
    bottom: "0",
  }
};

export const BackgroundContext = React.createContext({
  ground: moon,
  space: stars,
})

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      account: '0x0',
      loading: true,
      ground: moon,
      space: stars,
    }
    this.setGround = this.setGround.bind(this);
    this.setSpace = this.setSpace.bind(this);
    /* TODO: ADD CONTRACT ABI */
    //var LamboContract = web3.eth.contract();

    /* TODO: ADD CONTRACT ADDRESS */
    //var LamboAddy = LamboContract.at();
  }

  setGround(ground){
    this.setState({ground});
  }

  setSpace(space){
    this.setState({space})
  }

  componentWillMount(){
    this.loadUserWallet();
  }

  async loadUserWallet(){
    const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")
    const accounts = await web3.eth.getAccounts()
    this.setState({ account: accounts[0] });
    console.log(accounts);
  }

  render() {
    const { classes } = this.props;
    return (
        <BackgroundContext.Provider value={{
          ground: this.state.ground,
          space: this.state.moon,
          setGround: this.setGround,
          setSpace: this.setSpace,
        }}>
          <Router>
            <div className={classes.root} style={{backgroundImage: `url(${this.state.space})`,}}>
              <CssBaseline />
              <Container
                  className={classes.main}
                  style={{backgroundImage: `url(${this.state.ground})`,}}
                  component="main"
                  maxWidth={false}
                  disableGutters={true}
              >
                <Box className={classes.navContainer}>
                  <DegenNavbar />
                </Box>
                <Box className={classes.contentContainer}>
                  <Switch>
                    <Route exact path="/" component={Homepage} />
                    <Route exact path="/farm" component={DegenSwapPage} />
                    <Route exact path="/nitro" component={NitroPage} />
                    <Route exact path="/dealership" component={DealershipPage} />
                    <Route exact path="/degenswap" component={DegenSwapPage} />
                    <Route exact path="/about" component={About} />
                  </Switch>
                </Box>
                <Box className={classes.footerContainer}>
                  <DegenFooter />
                </Box>
              </Container>
            </div>
          </Router>
      </BackgroundContext.Provider>
    )
  }
}

export default withStyles(useStyles)(App);
