import React from "react";
import needle from "../images/needle.png";
import odometer from "../images/twap.png";
import {makeStyles} from "@material-ui/core/styles";
import {Box} from "@material-ui/core";


const useStyles = makeStyles((theme) => ({
    wrapper: {
        position: "relative",
        width: "100%",
        height: "100%",
    },
    odometer: {
        position: "absolute",
        width: "100%",
        height: "auto",
    },
    needle: {
        position: "absolute",
        width: "75%",
        top: "20px",
        left:"12.5%",
        height: "auto",
        zIndex: "10",
        transition: "transform 2s",
    }
}));

export default function Twap(props){
    const classes = useStyles();
    const range = (currentTwap) => {
        return currentTwap*255/50;
    }

    return (
        <Box className={classes.wrapper}>
            <img className={classes.needle} style={{transform: `rotate(${range(props.twap)}deg)`}} src={needle} alt={"Needle"}/>
            <img className={classes.odometer} src={odometer} alt={"odometer"}/>
        </Box>
    );
}
