/* React Imports */
import React, {useContext, useEffect} from 'react'
import Carousel from 'react-bootstrap/Carousel'
import { Helmet } from 'react-helmet'

/* Material Imports */
import { makeStyles } from '@material-ui/core/styles'

/* Images */
import moonSurface from '../images/moon-surface.png'
import blackLambo from '../images/lamborghini.png'
import whiteLambo from '../images/white-lambo.png'
import yellowLambo from '../images/lambo-yellow.png'
import redLambo from '../images/lambo-red.png'
import moon from '../images/moon-footer.png';
import earth from '../images/earth2.png';
/* CSS */
import '../App.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import {Grid, Typography} from '@material-ui/core'

import Box from '@material-ui/core/Box'
import {BackgroundContext} from "../App";
import stars from "../images/stars.png";

/* CSS Styles */
const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: '0vh',
    display: 'flex',
    flexDirection: 'column',
    minHeight: '95vh',
    color: 'white',
    justifyContent: "end",
  },
  carouselWrapper: {
    alignItems: 'center',
  },
  nftWrapper:{
    height: "70vh",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  nft: {
    width: '400px',
    [theme.breakpoints.up('md')]:{
      width: '750px',
    },
    [theme.breakpoints.up('lg')]:{
      width: '1000px',
    }
  },
}))

const nfts = [{
    src: blackLambo,
    name: "Lamborghini Huracan NFT",
    series: "Released 2020, Common",
    price: "0.5 $Lambo / $XXX USD"
  },
  {
    src: whiteLambo,
    name: "Lamborghini Gallardo NFT",
    series: "Released 2020, Uncommon",
    price: "1.6 $Lambo / $X,XXX USD"
  },
  {
    src: yellowLambo,
    name: "Lamborghini Centenario",
    series: "Released 2020, Very Rare",
    price: "3.2 $Lambo / $XX,XXX USD"
  },
  {
    src: redLambo,
    name: "Lamborghini Aventador",
    series: "Released 2020, Uncommon",
    price: "1.2 $Lambo / $X,XXX USD"
  }];

function DealershipPage() {
  /* App Functions */
  const classes = useStyles()
  const backgroundContext = useContext(BackgroundContext);
  useEffect(()=>{
    backgroundContext.setSpace(earth);
    backgroundContext.setGround(moon);
  },[])
  return (
      <>
        <Helmet>
          <meta charSet="utf-8" />
          <title>Dealership | Lambo Degenerates</title>
        </Helmet>
        <div
          className={classes.root}
        >
          <Typography align={"center"} variant={"p"}>Coming soon</Typography>
          <Typography align={"center"} component={"h2"} variant={"h2"}>Flex your gains</Typography>
          <Box className={classes.carouselWrapper}>
            <Carousel>
              {nfts.map(nft=>(
                  <Carousel.Item>
                    <div className={classes.nftWrapper}>
                      <img
                          className={classes.nft}
                          src={nft.src}
                          alt="First slide"
                      />
                    </div>
                    <Carousel.Caption>
                      <h3>{nft.name}</h3>
                      <p>{nft.series}</p>
                      <p>Current Price: {nft.price}</p>
                    </Carousel.Caption>
                  </Carousel.Item>
              ))}
            </Carousel>
          </Box>
        </div>
        </>
  )
}

export default DealershipPage
