/* React Imports */
import React, {useContext, useEffect, useState} from 'react'
import { Helmet } from 'react-helmet'

/* Material Imports */
import { makeStyles } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid'
import Box from '@material-ui/core/Box'
import { Typography } from '@material-ui/core'
import Button from '@material-ui/core/Button'

import Twap from './Twap'
import {BackgroundContext} from "../App";
import fire from '../images/fire.png'
import lambo from '../images/lambo-sideview.webp'
import Paper from '@material-ui/core/Paper'
import moon from '../images/moon-footer.png'
import stars from '../images/stars.png'

/* CSS Styles */
const useStyles = makeStyles((theme) => ({
  wrapper: {
    height: '100%',
    width: '100%',
    position: 'relative',
    alignItems: 'center',
  },
  contentWrapper: {
    margin: `${theme.spacing(5)}px auto`,
    maxWidth: '600px',
    padding: theme.spacing(3),
    color: 'white',
    backgroundColor: '#191919',
  },
  price: {
    textAlign: 'center',
    padding: theme.spacing(2),
  },
  claim: {
    textAlign: 'center',
    padding: theme.spacing(3),
  },
  twapWrapper: {
    height: '100%',
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    padding: theme.spacing(5),
  },
  twap: {
    width: '400px',
    height: '400px',
  },
  background: {
    position: 'absolute',
    width: '100%',
    height: '100%',
    overflow: 'hidden',
  },
  lambo: {
    width: '1000px',
    height: 'auto',
    transform: 'scaleX(-1)',
    bottom: '10px',
    margin: '0 auto',
  },
  fire: {
    position: 'absolute',
    width: '700px',
    height: 'auto',
    bottom: '10px',
    right: '40%',
  },
}))

function NitroPage() {
  const classes = useStyles()
  const backgroundContext = useContext(BackgroundContext);
  const [buyPrice, setBuyPrice] = useState(0);
  const [sellPrice, setSellPrice] = useState(0);
  const [twap, setTwap] = useState(0);
  const [availableNitro, setAvailableNitro] = useState(0);

  useEffect(()=>{
    backgroundContext.setSpace(stars);
    backgroundContext.setGround(moon);
  },[])

  return (
    <div className={classes.wrapper}>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Nitro | Lambo Degenerates</title>
      </Helmet>
      <Grid container style={{ alignItems: 'center' }}>
        <Grid item xs={12} md={6}>
          <Box
            px={3}
            className={classes.contentWrapper}
            borderRadius={'borderRadius'}
          >
            <Typography component={'h1'} variant={'h2'} align={'center'}>
              Nitro
            </Typography>
            <Typography className={classes.leftText} variant={'p'}>
              <ol>
                <li>
                  {' '}
                  Buy as the price goes up
                  <br />
                  a. Buyers are buying at a rate somewhere between current price
                  and TWAP
                </li>
                <li>
                  Sell as the price goes up <br />
                  a. Sellers can only sell at the TWAP <br />
                </li>
                <li>
                  Buy as the price goes down <br />
                  a. Buyers are buying at a rate much lower than current price{' '}
                </li>
                <li>
                  Sell as the price goes down <br />
                  a. Sellers are selling at a rate much lower than current price{' '}
                </li>
              </ol>
              Now, there are some things that the Nitro protocol has to do to
              prevent bots, whales, etc.
              <ol>
                <li>
                  1. Any bonus tokens rewarded to buyers are time-locked for 48
                  hours, and claimable at the $LAMBO website{' '}
                </li>
                <li>
                  {' '}
                  The tokens removed from sellers creating price volatility are
                  distributed like so: <br />
                  a. 25 % is sent to stakers at the $LAMBO website <br />
                  b. 75 % is sent to fund the Nitro protocol{' '}
                </li>
                <br />
              </ol>
              We’re not really sure exactly how the market is going to handle
              the Nitro protocol, all we know is that it’s hardwired to have
              numba go up.
            </Typography>
            <Grid container>
              <Grid item xs={6} className={classes.price}>
                <Typography variant={'h4'}>Buy price: {buyPrice}</Typography>
              </Grid>
              <Grid item xs={6} className={classes.price}>
                <Typography variant={'h4'}>Sell price: {sellPrice}</Typography>
              </Grid>
            </Grid>
            <Box className={classes.claim}>
              <Button
                variant="contained"
                color="primary"
                size={'large'}
                style={{
                  backgroundColor: '#FFF',
                  padding: '18px 36px',
                  fontSize: '18px',
                  color: 'black',
                }}
                disabled={true}
              >
                Claim {availableNitro} Nitro
              </Button>
            </Box>
          </Box>
        </Grid>
        <Grid item xs={12} md={6} className={classes.twapWrapper}>
          <Box className={classes.twap}>
            <Twap twap={twap} />
          </Box>
        </Grid>
      </Grid>
    </div>
  )
}

export default NitroPage
